/* ======= CARDS ======= */

.card,
.dcard {
    display: inline-block;
    width: 100%;
    background: $white;
    box-shadow: $level1;
    color: $darkgrey;
}

@media screen and (min-width: 420px) {
    .card,
    .dcard {
        max-width: 320px;
    }
}

.cols .card,
.cols .dcard {
    width: 100%;
    margin-bottom: 1rem;
    display: block;
}

/* Flex */

.card img,
.cols .card img {
    width: 100%;
    height: auto;
}

.card h1,
.cols .card h1 {
    font-family: $header;
    font-size: 1.3rem;
    font-weight: normal;
    line-height: 1.6rem;
    color: #black;
    padding: 1.8rem 1.2rem .2rem;
    margin: 0;
}

.card p,
.cols .card p {
    padding: 1rem 1.2rem 0;
    font-size: .8rem;
}

.card p:last-child,
.cols .card p:last-child {
    padding-bottom: 2rem;
}


/* Details */

.dcard:after {
    content: '';
    display: block;
    clear: both;
}

.dcard figure,
.cols .dcard figure {
    float: right;
    width: 30%;
    height: 0;
    overflow: hidden;
    padding-bottom: 30%;
    margin: .5rem .5rem .5rem 1rem;
}

.dcard figure img,
.cols .dcard figure img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.dcard h1,
.cols .dcard h1 {
    font-family: $header;
    font-size: .8rem;
    font-weight: 700;
    text-transform: uppercase;
    color: $black;
    padding: 1rem 1rem 0;
    margin: 0;
}

.dcard p,
.cols .dcard p {
    padding: .5rem 1rem .1rem;
    font-size: .7rem;
}

.dcard p:last-child,
.cols .dcard p:last-child {
    padding-bottom: 1rem;
}