/* ======= COLUMNS ======= */


.cols {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.cols>div {
    flex-basis: 100%;
    margin: 0 auto 50px; 
    color: $darkgrey;
}

@media screen and (min-width: 600px) {
    .cols.halves>div,
    .cols.thirds>div {
        flex-basis: 48%;
        margin: 0 0 40px;
    }
}

@media screen and (min-width: 800px) {
    .cols.thirds>div {
        flex-basis: 31%;
        margin: 0;
    }
}

.cols>div>figure {
    width: 100%;
    height: 0;
    overflow: hidden;
    padding-bottom: 60%;
}

.cols>div>figure img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.cols>div>h1 {
    font-family: $header;
    font-size: 1.3rem;
    font-weight: normal;
    line-height: 1.6rem;
    color: $black;
    padding: 1.8rem 0 .2rem;
    margin: 0;
}

.cols>div>p {
    width: 95%;
    font-size: .8rem;
    // Calculate line-height based on viewport width
    line-height: calc(1.2rem + .3 * ((100vw - 22.5rem ) / 40));
}

// Static line-height at full width
@media screen and (min-width: 1000px){
    .cols>div>p {
        line-height: 1.5rem;
    }
}

.cols>div>p:first-child {
    padding-top: 0;
}

.cols>div>p:last-child {
    padding-bottom: 2rem;
}