/* ======= BUTTONS ======= */


a.button {
    display: block;
}

.card a.button,
.cards a.button {
    margin-top: 1.5rem;
    padding: .8rem 0;
    background: transparent;
    border-top: 1px solid $grey;
    font-size: .7rem;
    font-weight: 700;
    letter-spacing: .1rem;
    text-transform: uppercase;
    text-align: center;
    color: $midgrey;
    transition: all .1s;
}

.card a.button:hover,
.cards a.button:hover {
    background: $blue;
    border-top: 1px solid $blue;
    color: $white;
}