/* ======= VARIABLES ======= */

/* -- Colors --  */

$lightblue: #c0ddf2;
$midblue: #30557e;
$blue: #002147;
$darkblue: #001228;

$lightgold: #fefada;
$midgold: #ffd453;
$gold: #c4960c;

$white: #ffffff;
$lightgrey: #f3f3f3;
$grey: #dddddd;
$semigrey: #cccccc;
$midgrey: #888888;
$darkgrey: #555555;
$black: #000000;

$link: #0047c2;
$linkhover: #003287;

/* -- Fonts -- */

$header: "Hind", sans-serif;

/* -- Levels -- */

$level1: 0 .1rem .3rem rgba(0,0,0,0.1);
$level2: 0 .3rem .5rem rgba(0,0,0,0.15);


/* ======= PARTIALS ======= */

@import 'columns';
@import 'tiles';
@import 'typography';
@import 'buttons';
@import 'cards';


/* ======= GLOBAL STYLES ======= */

* {
    box-sizing: border-box;
}